import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './Other/reportWebVitals';
import {BrowserRouter, Route, Routes} from "react-router-dom";
import App from "./MainPage/App";
import JavaCompile from "./javaProjects/JavaCompile";
import Python from "./pythonProjects/Python";
import Demo1 from "./webDesign/Demo1";
import Demo2 from "./webDesign/Demo2";
import TechStackOut from "./tecStack/TechStack";
import WebDesignOut from "./webDesign/WebDesign";
import FullStackOut from "./fullStack/FullStack";

ReactDOM.render(
    <React.StrictMode>
        <BrowserRouter>
            <div>
            <Routes>
                <Route path='/' component={<App/>} element={<App/>} />
                <Route path='/java' component={<App/>} element={<JavaCompile/>} />
                <Route path='/python' component={<App/>} element={<Python/>} />
                <Route path='/demo1' component={<App/>} element={<Demo1/>} />
                <Route path='/demo2' component={<App/>} element={<Demo2/>} />
                <Route path='/tech-stack' component={<App/>} element={<TechStackOut/>} />
                <Route path='/web-design' component={<App/>} element={<WebDesignOut/>} />
                <Route path='/full-stack' component={<App/>} element={<FullStackOut/>} />
                <Route path='/ride-sharing' component={() => {
                    window.location.replace('https://mhal1.github.io/ridesharing');
                    return null;
                }}/>
                <Route path='/*' component={<App/>} element={<App/>} />
            </Routes>
            </div>
        </BrowserRouter>
    </React.StrictMode>,
    document.getElementById('root')
);


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
